<template>
    <div class="guanwang">
        <img width="100%" src="https://www.lianaizhuli.com/gongzhonghao/guanwang.png" />
        <a href="https://www.lianaizhuli.com/gongzhonghao/Love-Union.apk">
            <img class="andorid" v-bind:style="andoridobj" src="../assets/icon_android.png">
        </a>
        <img class="xiaochengxuma" v-bind:style="xiaochengxumaobj" src="https://www.lianaizhuli.com/gongzhonghao/xiaochengxuma.png" />
        <div class="dibu">CopyRights © 2018-2019 深圳宇子科技有限公司版权所有，禁止非法复制 经营许可证编号：粤ICP备18124010号</div>
    </div>
</template>

<script>
    export default {
        async mounted() {
            this.screenWidth=document.body.clientWidth;
            this.xiaochengxumaobj.top=1920/this.screenWidth*16+'px';
            this.andoridobj.top=1920/this.screenWidth*16+'px';
        },
        async created() {},
        data() {
            return {
                screenWidth:0,
                xiaochengxumaobj:{
                    position: 'absolute',
                    width:'10%',
                    left:'70%',
                    top:'0%',
                },
                andoridobj:{
                    position: 'absolute',
                    width:'10%',
                    left:'48%',
                    top:'0%',
                },
            }
        },
        methods: {},
    }
</script>

<style lang="scss">
    .guanwang {
        .dibu {
            text-align: center;
            margin-bottom: 50px;
        }
    }
</style>



